.logo_menu {
    position: absolute;
    gap: 270px;
    max-width: 970px;
    padding: 0 15px;
    margin-top: 31px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;

    .logo {
        pointer-events: none;
    }
}

.content-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 774px;
    opacity: 1;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    z-index: 1;

    .clients {
        display: flex;
        gap: 12px;
        color: #ffffff;
        font-family: "PF Encore Sans Pro Bold", sans-serif;
        font-size: 51px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 36px;
        text-align: left;
        text-transform: uppercase;
        z-index: 3;
        margin-bottom: 0;
        span {
            color: #fc635e;
        }


    }

    .text-hero {
        color: #ffffff;
        font-family: "PF Encore Sans Pro Light", sans-serif;
        font-size: 24px;
        font-weight: 300;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        z-index: 3;
    }
    .bg {
        background: #181818;
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0.7;
    }
}
.service-section{
    position: relative;
    padding-top: 40px;
    margin-top: 27px;
    .service-content{

        .service-title{
            span {
                color: #fc635e;
            }
            max-width: 342px;
            position: relative;
            color: #868686;
            font-family: "PF Encore Sans Pro Medium", sans-serif;
            font-size: 28px;
            font-weight: 500;
            font-style: normal;
            letter-spacing: normal;
            line-height: 26px;
            text-align: center;
            text-transform: uppercase;
            margin: 0 auto;
            &:after{
                position: absolute;
                content: "";
                width: 100%;
                height: 1px;
                background-color: #adadad;;
                bottom: -11px;
                left: 0;
            }
        }



        .service-subtitle{
            color: #adadad;
            max-width: 505px;
            font-family: "PF Encore Sans Pro - Thin", sans-serif;
            font-size: 20px;
            font-weight: 300;
            font-style: normal;
            letter-spacing: normal;
            line-height: 26px;
            text-align: center;
            margin: 24px auto;
        }
    }
}

